import React, { Component } from "react";
import "./TodoItem.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

class TodoItem extends Component{
  render(){
    //1. this.props.text
    //2. const text = this.props;
    //3. this. props.text를 text에 넣어라
    const {text,id,onRemove,onToggle,checked} = this.props;

    return( 
      <div className="todoItem" onClick={()=>onToggle(id)}>
        <div className="remove" onClick={(e)=>{e.stopPropagation(); onRemove(id)}}><FontAwesomeIcon icon={faTimes} className="fTimes" /></div>
        <div className={`todoText ${checked && 'checked'}`}>
          {/*1. {this.props.text} */}
          {/*2. {text.text} */}
          {/* 3. {text} */}
          {text}
        </div>
        <div className="check"><FontAwesomeIcon icon={faCheck} className="fCheck" /></div>
      </div>
    );
  }
}

export default TodoItem;