// import './App.css';
import React, {Component} from "react";
import TodoListTemplate from "./components/TodoListTemplate";
import Form from "./components/Form";
import TodoItemList from "./components/TodoItemList";


class App extends Component{
  //할일을 체크할 변수 만들기
  id = 3;
  state = {
    input:"",
    todos:[
      {id:0,text:"리액트 정리",checked:false},
      {id:1,text:"프로젝트 정리",checked:false},
      {id:2,text:"포트폴리오 정리",checked:false}
    ]
  }

  onChange = (e) => {
    this.setState({
      input:e.target.value
    })
  }

  listAdd = (e) => {
    const {input,todos} = this.state;

    if(input === "")
      alert("할 일을 입력하세요!");
    else
      this.setState({
        input:"",
        todos:todos.concat({
          id: this.id++,
          text:input,
          checked:false
        })
      })    
  }

  listRemove = (id) => {
    this.setState({
      todos:this.state.todos.filter(todo => todo.id !== id)
    })
  }

  listToggle = (id) =>{
    //id로 받은 요소의 값이 몇번째인지 찾아서 그 값에 해당하는 요소의 checked값을 반전 findIndex순서 찾기
    const index = this.state.todos.findIndex(todo => todo.id === id);
    //index번째 요소값을 저장할 변수
    const selected = this.state.todos[index];
    //checked 속성만 반전해서 저장할 배열 만들기
    //todos 배열을 복사
    //배열 전체 복사하기 [...배열명]
    var newTodos = [...this.state.todos];
    newTodos[index] = {
      ...selected,
      checked : !selected.checked
    }

    this.setState({
      todos:newTodos
    })
  }

  onKeyEnter = (e) => {
    console.log("enter")
    if(e.key === "Enter"){
      this.listAdd();
    }
  }

  render(){
    return(
      <div>
        <TodoListTemplate form={<Form value={this.state.input} onAppChange={this.onChange} onCreate={this.listAdd} onAppKey={this.onKeyEnter} />}>
          <TodoItemList todos={this.state.todos} onRemovep={this.listRemove} onTogglep={this.listToggle} />
        </TodoListTemplate>
      </div>
    )
  }
}


export default App;
