import React from "react";
import "./Form.css";

const Form = ({value,onAppChange,onCreate,onAppKey}) => {
  return(
    <div className="inputForm">
      <input value={value} onChange={onAppChange} onKeyPress={onAppKey}></input>
      <button className="addButton" onClick={onCreate}>추가</button>
    </div>
  )
}

export default Form;